class ToolBrower{
    constructor(){

    }

    /**
     * [formatDate 事件格式化]
     * @param  {[type]} cdate [new Date()]
     * @param  {[type]} fmt   [格式]
     * @return {[type]}       [时间格式化结果]
     */
    formatDate(cdate,fmt){

            var o = {
                "M+": cdate.getMonth() + 1, //月份 
                "d+": cdate.getDate(), //日 
                "h+": cdate.getHours(), //小时 
                "m+": cdate.getMinutes(), //分 
                "s+": cdate.getSeconds(), //秒 
                "q+": Math.floor((cdate.getMonth() + 3) / 3), //季度 
                "S": cdate.getMilliseconds() //毫秒 
            };
            if (/(y+)/.test(fmt)) {
                fmt = fmt.replace(RegExp.$1, (cdate.getFullYear() + "").substr(4 - RegExp.$1.length));
            }
            for (var k in o)
                if (new RegExp("(" + k + ")").test(fmt)) 
                    fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
            return fmt; 
    }

    /**
     * [lset localStorage setting]
     * @param  {[type]} name  [description]
     * @param  {[type]} value [description]
     * @return {[type]}       [description]
     */
    lset(name,value){
        localStorage.setItem(name, JSON.stringify(value));
    }
    lget(name) {
        return JSON.parse(localStorage.getItem(name))
    }
    ladd(name, addVal) {
        let oldVal = this.lget(name)
        let newVal = oldVal.concat(addVal)
        this.lset(name, newVal)
    }

    /**
     * [sset sesssionStorage setting]
     * @param  {[type]} name  [description]
     * @param  {[type]} value [description]
     * @return {[type]}       [description]
     */
    sset(name,value){
        sessionStorage.setItem(name, JSON.stringify(value));
    }
    sget(name) {
        return JSON.parse(sessionStorage.getItem(name))
    }
    sadd(name, addVal) {
        let oldVal = this.sget(name)
        let newVal = oldVal.concat(addVal)
        this.sset(name, newVal)
    }



    /**
     * [addZoom 补零几位]
     * @param {[String]} str [原字符串]
     * @param {[Number]} len [补几位]
     */
    addZoom(str,len){

        let newStr = str+'';
        let dis = 0;

        dis = len - newStr.length;
        newStr =  str;
        for(var i=0;i<dis;i++){
            newStr = '0'+newStr;
        }

        return newStr;
    }


    /**
     * [getTime 获取时间]
     * @return {[Oject]} [时间对象]
     */
    getTime(){
        let date = new Date();
        let d = {};

        var year = date.getFullYear();
        var month = date.getMonth()+1;
        var day = date.getDate();
        var week = date.getDay();

        var hours = this.addZoom(date.getHours(),2);
        var minute = this.addZoom(date.getMinutes(),2);
        var section = this.addZoom(date.getSeconds(),2);

        return {
            year: year,
            month: month,
            day: day,
            week: week,
            hours: hours,
            minute: minute,
            section: section
        };
    }
    
}

export default new ToolBrower();


